import { Component, OnInit } from '@angular/core'
import { CommonModule } from '@angular/common'
import { FormsModule } from '@angular/forms'
import {
  IonContent,
  IonList,
  IonItem,
  IonRow,
  IonCol,
  IonText,
  IonNote,
  IonIcon,
  IonButton,
  IonSpinner,
  IonLabel,
} from '@ionic/angular/standalone'
import { NavController } from '@ionic/angular'
import {
  arrowBackOutline,
  batteryFull,
  batteryHalf,
  batteryDead,
  batteryFullOutline,
  closeOutline,
  informationCircleOutline,
} from 'ionicons/icons'
import { addIcons } from 'ionicons'
import {
  ListStation200ResponseAllOfDataInner,
  ListVehicule200ResponseAllOfDataInner,
  VehiculesService,
} from 'src/api-cyclist'
import { StorageService } from 'src/app/services/utils/storage.service'
import { environment } from 'src/environments/environment'
import { StationWrapperService } from 'src/app/services/api-wrappers/stations.service'
import { SearchbarComponent } from 'src/app/components/searchbar/searchbar.component'
import { TranslateModule } from '@ngx-translate/core'
import {
  SearchState,
  SearchWrapperService,
} from 'src/app/services/api-wrappers/search.service'
import { RentingService } from 'src/app/services/api-wrappers/renting.service'
import { AccountService } from 'src/app/services/api-wrappers/account.service'

@Component({
  selector: 'app-search',
  templateUrl: './search.page.html',
  styleUrls: ['./search.page.scss'],
  imports: [
    IonContent,
    CommonModule,
    FormsModule,
    IonList,
    IonText,
    IonItem,
    IonNote,
    IonIcon,
    IonButton,
    IonSpinner,
    IonLabel,
    SearchbarComponent,
    IonRow,
    IonCol,
    TranslateModule,
  ],
})
export class SearchPage implements OnInit {
  stations: ListStation200ResponseAllOfDataInner[] = []
  searchState: SearchState = {
    state: 'loading',
  }
  detailedVehicles: Map<string, ListVehicule200ResponseAllOfDataInner> =
    new Map()
  loadingVehicles: Set<string> = new Set()

  constructor(
    private navCtrl: NavController,
    private storage: StorageService,
    private stationWrapperService: StationWrapperService,
    private searchWrapperService: SearchWrapperService,
    private rentingService: RentingService,
    public accountService: AccountService,
    private vehiculesService: VehiculesService
  ) {
    this.searchWrapperService.searchState.subscribe((state) => {
      this.searchState = state
      if (state.state === 'success') {
        const filteredVehicles = state.results.vehicules.filter(
          (vehicule) => vehicule.status === 'service'
        )
        this.searchState = {
          state: 'success',
          results: {
            stations: state.results.stations.filter(
              (station) => station.status === 'open'
            ),
            vehicules: filteredVehicles,
          },
        }
        // Load detailed information for each vehicle
        filteredVehicles.forEach((vehicle) => {
          if (vehicle.id) {
            this.loadVehicleDetails(vehicle.id)
          }
        })
      }
    })
    addIcons({
      arrowBackOutline,
      batteryFull,
      batteryHalf,
      batteryDead,
      batteryFullOutline,
      closeOutline,
      informationCircleOutline,
    })
  }
  async ngOnInit() {
    const stationsData = await this.storage.get('stations')
    this.stations = stationsData?.data || []
    this.searchState = {
      state: 'success',
      results: {
        stations: this.stations,
        vehicules: [],
      },
    }
  }

  handleSearch(query: string) {
    const searchTerm = query.toLowerCase().trim()
    if (!searchTerm || searchTerm === '' || searchTerm.length == 0) {
      this.searchState = {
        state: 'success',
        results: {
          stations: this.stations,
          vehicules: [],
        },
      }
      return
    }
    this.searchWrapperService.search(searchTerm)
  }
  openStation(station: ListStation200ResponseAllOfDataInner) {
    this.navCtrl
      .navigateForward('/map', {
        animated: false,
      })
      .then(() => {
        this.stationWrapperService.setStationState('station-selected', station)
      })
  }

  onSearchbarStateChange() {
    this.searchState = { state: 'idle' }
  }

  getBatteryIcon(vehicle: ListVehicule200ResponseAllOfDataInner): string {
    const percent = vehicle?.battery_vae?.percent
    if (percent === undefined || percent === null) {
      return 'battery-full-outline'
    }
    if (percent > 70) return 'battery-full'
    if (percent > 30) return 'battery-half'
    return 'battery-dead'
  }

  getBatteryColor(vehicle: ListVehicule200ResponseAllOfDataInner): string {
    const percent = vehicle?.battery_vae?.percent
    if (percent === undefined || percent === null) {
      return 'medium'
    }
    if (percent > 70) return 'success'
    if (percent > 30) return 'warning'
    return 'danger'
  }

  async rentVehicle(vehicle: ListVehicule200ResponseAllOfDataInner) {
    if (!vehicle) {
      return
    }
    this.rentingService.startRenting(
      vehicle.id ? this.detailedVehicles.get(vehicle.id) || vehicle : vehicle
    )
  }

  loadVehicleDetails(vehicleId: string) {
    if (
      !vehicleId ||
      this.detailedVehicles.has(vehicleId) ||
      this.loadingVehicles.has(vehicleId)
    ) {
      return
    }

    this.loadingVehicles.add(vehicleId)
    this.vehiculesService
      .retrieveVehicule({
        program: environment.program,
        id: vehicleId,
      })
      .subscribe({
        next: (vehicle) => {
          if (vehicle && vehicleId) {
            this.detailedVehicles.set(vehicleId, vehicle)
          }
          this.loadingVehicles.delete(vehicleId)
        },
        error: (error) => {
          console.error('Error loading vehicle details:', error)
          this.loadingVehicles.delete(vehicleId)
        },
      })
  }

  isVehicleLoading(vehicle: ListVehicule200ResponseAllOfDataInner): boolean {
    if (!vehicle?.id) {
      return false
    }
    return this.loadingVehicles.has(vehicle.id)
  }
}
